import Link from "next/link"
import { Component } from 'react'

const ulStyle = {
    border: "0px solid red"
}

const liStyle = {
    listStyleType: "none",
    padding: "2em",
    display: "flex",
    flexDirection: "column",
    border: "1px solid red"
}

const successStyle = {
    background: "#bceda6",
    display: "inline-block",
    listStyleType: "none",
    width: "100%",
    paddingTop: "0.5em",
    paddingBottom: "0.5em"
}

const errorStyle = {
    background: "#eaa4a4",
    display: "inline-block",
    listStyleType: "none",
    width: "100%",
    paddingTop: "0.5em",
    paddingBottom: "0.5em"
}

const messageStyle = {
    marginLeft: "1em",
    fontWeight: "600",
    color: "#333"
}

const wineFragment = {
    border: "1px solid red",
    display: "flex",
    justifyContent: "space-evenly"
}

class WineList extends Component {
    constructor(props) {
        super(props)

        this.state = {
            edit: false,
            recentlyEdited: false
        }
    }

    deleteWine = (wine) => {
        fetch("http://localhost:3001/wines/" + wine, { method: "DELETE" })
        .then( data => data.json())
        .then( res => {
            // Refresh Wines List
            this.props.updateWineList()
            this.displayInformation(res)
        })
    }

    editWine = (data) => {
        console.log(data)
        let query = ""

        if(data.name) {
            query += "name=" + data.name
            if(data.price) query += "&price=" + data.price
            if(data.currency) query += "&currency=" + data.currency
            if(data.volume) query += "&volume=" + data.volume
            if(data.origin) query += "&origin=" + data.origin
            if(data.alcoholPercentage) query += "&alcoholPercentage=" + data.alcoholPercentage
            if(data.description) query += "&description=" + data.description   
        }

        fetch("http://localhost:3001/wines?" + query, { method: "PUT" })
        .then( data => data.json())
        .then( res => {
            // Refresh wine list and remove edit data
            this.props.updateWineList()
            this.setState({ edit: false, data: null }, () => { this.displayInformation(res) })
        })
    }

    displayInformation(res) {
        this.setState({ res }, () => {
            setTimeout( () => {
                this.setState({ res: null })
            }, 2000)
        })
    }

    handleInput = (event) => {
        let changedKey = event.target.name
        let data = {
            ...this.state.data
        }
        
        data[changedKey] = event.target.value
        this.setState({ data })
    }

    render() {
        return (
            <ul style={ulStyle}>
                { this.state.res 
                    ?   this.state.res.Message 
                        ?   (<li style={successStyle}>
                                <p style={messageStyle}>{ this.state.res.Message }</p>
                            </li>) 
                        :   (<li style={errorStyle}>
                                <p style={messageStyle}>{ this.state.res.Error }</p>
                            </li>)
                    : null } 
                { this.props.wines.map( wine => (
                    this.state.edit ? 
                        this.state.data.tempName === wine.name ? 
                            <li style={liStyle} key={wine.name}>
                                <h3>Editing {wine.tempName}</h3>
                                <label>Name</label><br />
                                <input name="name" type="text" value={ this.state.data.name } onChange={ this.handleInput } /><br />
                                <label>Price</label><br />
                                <input name="price" type="text" value={ this.state.data.price } onChange={ this.handleInput } /><br />
                                <label>Currency</label><br />
                                <input name="currency" type="text" value={ this.state.data.currency } onChange={ this.handleInput } /><br />
                                <label>Volume</label><br />
                                <input name="volume" type="text" value={ this.state.data.volume } onChange={ this.handleInput } /><br />
                                <label>Origin</label><br />
                                <input name="origin" type="text" value={ this.state.data.origin } onChange={ this.handleInput } /><br />
                                <label>Alcohol</label><br />
                                <input name="alcoholPercentage" type="text" value={ this.state.data.alcoholPercentage } onChange={ this.handleInput } /><br />
                                <label>Description</label><br />
                                <input name="description" type="text" value={ this.state.data.description } onChange={ this.handleInput } />
                                <button onClick={ () => { this.editWine(this.state.data) } } >Confirm</button>
                                <button onClick={ () => { this.setState({ edit: false, data: null }) } } >Discard</button>
                            </li>
                        :   null
                    :   <li style={liStyle} key={wine.name}>
                            <div style={wineFragment}>
                                <div style={wine}>
                                    <h3>{ wine.name }</h3>
                                    <p>Origin: { wine.origin }</p>
                                </div>
                                <div>
                                    <h4>{ wine.price + wine.currency }</h4>
                                    <p>{ wine.alcoholPercentage } %, { wine.volume }</p>
                                </div>
                            </div>
                            <div style={wineFragment}>
                                <p>{ wine.description }</p>
                            </div>
                            <button className="edit" onClick={ () => { 
                                let data = {
                                    ...wine,
                                    tempName: wine.name
                                }

                                this.setState({ edit: true, data })
                            }} >Edit</button>
                            <button className="delete" onClick={ () => { this.deleteWine(wine.name) } } >Delete</button>
                        </li>
                )) }
            </ul>
        )
    }
}

export default WineList


